import React from 'react'
import TextField from '@material-ui/core/TextField';
import Autocomplete from '@material-ui/lab/Autocomplete';

const SearchRackets = ({ rackets, selectedRackets, handleSubmit }) => {
  return (
    <>
      <Autocomplete
        multiple
        id="rackets-input"
        onChange={handleSubmit}
        options={rackets}
        getOptionLabel={(option) => option.name}
        getOptionDisabled={option => {
          if (selectedRackets.length === 2) {
            return true
          } else {
            return false
          }
        }}
        renderInput={(params) => (
          <TextField
            {...params}
            variant="standard"
            label="Select rackets"
            placeholder={selectedRackets.length === 2 ? '' : 'Select two rackets'}
          />
        )}
      />
    </>
  )
}

export default SearchRackets